export default {
  props: {
    id: {
      required: true,
      default: null,
      type: String,
    },
  },
  data: () => ({
    blogData: {
      heading: '',
      subheading: '',
      info: [],
      categories: [],
      description: [],
      img: '',
    },
    blogs: [],
    errorMessages: [],
    heading: '',
    subheading: '',
    description: '',
    info: '',
    categories: [],
    image: '',
    img: [],
    formHasErrors: false,
    imageHeading: ['image', ''],
    items: ['Fibre Blowing', 'Klein Tools', 'Utilities', 'Telecoms'],
    category: '',
  }),
  watch: {
    name() {
      this.errorMessages = []
    },
  },
  created() {
    this.getBlog()
    this.getAllBlogs()
  },
  methods: {
    async getAllBlogs() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const response = await this.$axios.get(
          'https://cbsbackend.herokuapp.com/api/blogs',
          config
        )

        for (const blog of response.data) {
          this.blogs.push(blog.heading)
        }

        return this.blogs
      } catch (err) {
        throw new Error('Error Fetching Products')
      }
    },
    addCategory() {
      // console.log(this.category)
      this.categories = this.category
    },
    async getBlog() {
      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        const { data } = await this.$axios.get(
          `https://cbsbackend.herokuapp.com/api/blogs/${this.id}`,
          config
        )

        this.blogData = {
          ...data,
        }

        return this.blogData
      } catch (error) {
        throw new Error(error)
      }
    },
    async saveBlog() {
      const data = {
        user: this.$auth.user,
        ...this.blogData,
      }

      const config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
        },
      }
      try {
        await this.$axios.put(
          `https://cbsbackend.herokuapp.com/api/blogs/${this.id}`,
          data,
          config
        )
        // this.getAllMusics()
      } catch (err) {
        throw new Error('Error updating product')
      }
    },
    async saveBlogImages(event) {
      const input = event.target.files[0]
      const formData = new FormData()

      formData.append('image', input)

      try {
        const config = {
          headers: {
            'Content-Type': 'multipart/form-data',
            'Access-Control-Allow-Origin': '*',
          },
        }

        const { data } = await this.$axios.post(
          'https://cbsbackend.herokuapp.com/api/upload/',
          formData,
          config.headers
        )

        this.image = data

        this.img.push({ image: this.image })
      } catch (error) {
        throw new Error(error)
      }
    },
    deleteBlogImage(index) {
      this.img.splice(index, 1)
    },
  },
}
